.formContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 280px;
    min-height: 473px;
    background-color: #fff;
    box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
    border-radius: 20px;
    padding: 40px 12px;
}

@media screen and (min-width: 1280px) {
    .formContainer {
        width: 608px;
        min-height: 481px;
        border-radius: 40px;
        padding: 60px 75px;
    }
}
